
.upload_input {
    color: transparent;
}

.upload_input::-webkit-file-upload-button {
    visibility: hidden;
}

.upload_input::before {
    content: 'Upload photo';
    color: #009688;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    font-size: 20px;
    font-family: Montserrat;
    outline: none;
    cursor: pointer;
}

.upload_input:focus{
    outline: none;
}


